import { CHOICE_DELIVERY } from "./action";

const initialState = {
  deliveryMethod: "DHL",
  allMethods: [
    {
      id: 0,
      nameMethod: "DHL",
      timeToPost: "Today",
      image: "https://cargotime.ru/wp-content/uploads/2017/01/dhl.png",
    },
    {
      id: 1,
      nameMethod: "UDH",
      timeToPost: "3 Days",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSFcFI14AOIaEnUcRxh4toSNz7mBrPO6U_9fA&usqp=CAU",
    },
    {
      id: 2,
      nameMethod: "DPD",
      timeToPost: "1-2 Days",
      image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRXCbwomcXBpkRYk2U2Gfp7vDLMvFyP6shvjA&usqp=CAU",
    },
    {
        id: 3,
        nameMethod: "HFG",
        timeToPost: "5 Days",
        image: "data:image/png;base64,"
    },
  ],
};

export const deliveryReducer = (state = initialState, action) => {
  switch (action.type) {
    case CHOICE_DELIVERY:
      return ({
        ...state,
        deliveryMethod: action.payload.deliveryMethod,
      })
    default:
      return state;
  }
};